import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		// baseUrl:'http://192.168.8.106/',
		baseUrl: '',
		invoiceBaseUrl: '', //
		baseImgUrl: '', //
		appkey: '',
		login_token: '12123', //token

		hasLogin: true, //
		hasBoundHouse: true, //
		myHouse: '', //
		hasAuthorized: false, //
		wxUserInfo: '', //

		city: {}, //
		village: {}, //
		tower: {}, //
		unit: {}, //
		room: {}, //

		myRoom: {}, //
		changeCar: '', //
		carData: {}, //
		roomData: {}, //
		orderData: '', //
		accountData: '', //

		qiniuData: '', //
		contacts: '', //
		familyData: '', //
		publicData: '', //

		pushMessage:'', //
	},
	mutations: {
		updatePushMessage(state, message) {
			/**
			 *  payload 
			 *  payload 
			 */
			// let payload = message.payload;
			// if (typeof payload !== 'string') {
			// 	message.payload = JSON.stringify(payload);
			// }
			state.pushMessage = message || {};
		},
		bindHouse(state, data) { //
			state.hasBoundHouse = true;
			try {
				uni.setStorageSync('hasBoundHouse', true);
			} catch (e) {
				// error
			}
		},
		setQiniuData(state, data) {
			state.qiniuData = data;
		},
		setAccountData(state, data) {
			state.accountData = data;
		},
		setFamilyData(state, data) {
			state.familyData = data;
		},
		setPublicData(state, data) {
			state.publicData = data;
		},
		setContacts(state, data) {
			state.contacts = data;
		},
		setHasLogin(state, data) { //
			state.hasLogin = true;
		},
		setMyRoom(state, data) {
			state.myRoom = data;
		},
		loginToken(state, data) { //token
			state.login_token = data.login_token;
			state.hasLogin = true;
			try {
				uni.setStorageSync('hasLogin', true);
			} catch (e) {
				// error
			}
		},
		loginWeChat(state, userInfo) { //
			if (userInfo.openId !== null) {
				state.hasAuthorized = true;
				state.wxUserInfo = userInfo;
				try {
					uni.setStorageSync('wxUserInfo', userInfo);
				} catch (e) {
					// error
				}
			}
		},
		logout(state) { //
			state.hasLogin = false;
			state.myHouse = '';
			state.login_token = null;
			try {
				uni.removeStorageSync('hasLogin');
				uni.removeStorageSync('wxUserInfo');
				uni.removeStorageSync('loginToken');
			} catch (e) {
				// error
			}
		},
		setChangeCar(state, data) {
			state.changeCar = data
		},

		setCity(state, data) { //
			state.city = data
			state.village = ''
			state.tower = ''
			state.unit = ''
			state.room = ''
		},
		setVillage(state, data) { //
			state.village = data
			state.tower = ''
			state.unit = ''
			state.room = ''
		},
		setTower(state, data) { //
			state.tower = data
			state.unit = ''
			state.room = ''
		},
		setUnit(state, data) { //
			state.unit = data
			state.room = ''
		},
		setRoom(state, data) { //
			state.room = data
		},

		setCarData(state, data) {
			state.carData = data;
		},
		setRoomData(state, data) {
			state.roomData = data;
		},
		setMyHouse(state, data) {
			console.log("data: " + JSON.stringify(data));
			state.hasLogin = true;
			if (data.ownerInfo.roomid) {
				state.hasBoundHouse = true;
				state.myHouse = data;
			} else {
				state.hasBoundHouse = false;
			}
		},
		setOrderData(state, data) {
			state.orderData = data;
		}
	},
	actions: {
		loginToken(context) {
			context.commit('loginToken')
		},
		loginWeChat(context) {
			context.commit('loginWeChat')
		},
		setTower(context) {
			context.commit('setTower')
		},
		setUnit(context) {
			context.commit('setUnit')
		},
		setRoom(context) {
			context.commit('setRoom')
		},
		setMyRoom(context) {
			context.commit('setMyRoom')
		}
	}
})

export default store